
.loginImg{
  width: 100vw;
  height: 100vh;
  background-image: url('../../img/bei2.jpeg');
  background-repeat:no-repeat;
  .con{
    width: 400px;
    height: 300px;
    background: white;
    position: relative;
    top: calc(50% - 250px);
    left: calc(50% - 200px);
    border-radius: 5px;
    .header{
      display: flex;
      justify-content: space-around;
      p{
        margin-top: 10px;
        padding: 0 10px;
        &.sed{
          background: wheat;
          border-radius: 10px;
        }
      }
    }
    .inp{
      input{
        border-bottom: 1px solid #eaeaea !important;
        width: 220px;
        margin: 20px;
      }
      .active{
        color: red;
      }
      .daojishi{
        display: flex;
      }
      .code_span{
        padding: 5px;
        border: 1px solid rgb(34, 188, 123);
        color: rgb(34, 188, 123);
        border-radius: 10px;
        font-size: 10px;
        display: flex;
        height: 30px;
      }
      .code_span_s{
        padding: 5px;
        border: 1px solid rgba(32, 34, 33);
        color: rgba(32, 34, 33);
        border-radius: 10px;
        font-size: 10px;
        display: flex;
        height: 30px;
      }
      .wangPss{
        font-size: 8px;
        color: #777373;
        margin-left: 300px;
        margin-top: 0;
        padding-top: 0;
      }
    }
    .btn{
      margin-left: calc(50% - 50px);
    }
  }
  .ant-statistic-content{
    font-size: 12px;
  }
  .ant-statistic{
    display: flex;
  }
}