.SetPass_box{
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  // background: white;
  background-image: url('../../img/bei2.jpeg');
  .content{
    width: 450px;
    height: 350px;
    background-color: white;
    position: fixed;
    left: calc(50% - 250px);
    top: calc(50% - 150px);
    padding-left: 20px;
    border-radius: 5px;
    .header{
      height: 50px;
      width: 100%;
      text-align: center;
      line-height: 50px;
      font-size: 16px;
    }
    .list{
      p{
        input{
          width: 220px;
          margin: 20px;
          border-bottom: 1px solid #eaeaea !important;
        }
        .active{
          color:red;
        }
        .list_p_span{
          color: rgb(34, 188, 123);
          padding: 5px;
          border: 1px solid rgb(34, 188, 123);
          border-radius: 15px;
          font-size: 8px;
          display: flex;
          height: 30px;
        }
        .list_p_span_s{
          color: rgb(45, 54, 50);
          padding: 5px;
          border: 1px solid rgba(45, 54, 50);
          border-radius: 15px;
          font-size: 8px;
          display: flex;
          height: 30px;
        }
      }
      .daojishi{
        display: flex;
      }
    }
    .btn{
      display: flex;
      justify-content: space-around;
    }
  }
  .ant-statistic-content{
    font-size: 12px;
  }
}